* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  max-width: 700px;
  margin: 100px auto 0;
}
.search-box {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}
.search-box .form-control {
  border: 1px solid #ddd;
  padding: 10px 10px 10px 30px;
  border-radius: 5px;
  margin: 0;
  width: 100%;
}
.search-input {
  position: relative;
  width: 35%;
  margin: 0 0 5px 0;
}
.search-input::before {
  content: '';
  background: url('/src/images/search.svg') no-repeat;
  position: absolute;
  left: 8px;
  top: 11px;
  width: 20px;
  height: 20px;
}
.search-box .form-control:focus {
  outline: 1;
  border: 1px solid #aaa;
}
.friend-list-wrapper {
  display: flex;
  flex-flow: column wrap;
  width: 100%;
  border: 1px solid #ddd;
}
.friend-list-wrapper h2 {
  background: #dfdfdf;
  font-size: 20px;
  padding: 10px 20px;
  line-height: 20px;
}
.form-control {
  margin: 10px 0;
}
.cards-list-wrap {
  display: flex;
  flex-flow: column wrap;
  border-top: 1px solid #ddd;
}
.list-item {
  display: flex;
  padding: 10px 20px;
  border-top: 1px solid #ddd;
}
.info-box {
  width: 80%;
  flex-flow: column wrap;
}
.info-box h4 {
  font-size: 14px;
  margin-bottom: 5px;
}
.info-box p {
  font-size: 12px;
  color: #999;
}
.action-buttons {
  display: flex;
  width: 20%;
  justify-content: flex-end;
  align-items: center;
}
.action-buttons img {
  width: 25px;
  height: 25px;
}
.action-buttons span {
  padding: 4px;
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 55px;
  display: inline-block;
  text-align: center;
  cursor: pointer;
}
.delete-icon {
  margin-left: 10px;
}
.form-control {
  margin: 10px 20px;
  padding: 5px 0;
  border: 0;
}
.form-control:focus {
  border: 0;
  outline: 0;
}
.no-record {
  color: red;
  padding: 10px 20px;
}
.pagination-wrap {
  display: flex;
  justify-content: center;
}
.pagination {
  display: inline-block;
  padding-left: 0;
  margin: 20px 0;
  border-radius: 4px;
}
.pagination>li {
  display: inline;
}
.pagination>li>a,
.pagination>li>span {
  position: relative;
  float: left;
  padding: 6px 12px;
  margin-left: -1px;
  line-height: 1.42857143;
  color: #337ab7;
  text-decoration: none;
  background-color: #fff;
  border: 1px solid #ddd;
}
.pagination>.disabled>a, 
.pagination>.disabled>a:focus, 
.pagination>.disabled>a:hover, 
.pagination>.disabled>span, 
.pagination>.disabled>span:focus, 
.pagination>.disabled>span:hover {
  color: #777;
  cursor: not-allowed;
  background-color: #fff;
  border-color: #ddd;
}
.pagination>li:first-child>a, 
.pagination>li:first-child>span {
  margin-left: 0;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.pagination>li:last-child>a, 
.pagination>li:last-child>span {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
.pagination>.active>a, 
.pagination>.active>a:focus, 
.pagination>.active>a:hover, 
.pagination>.active>span, 
.pagination>.active>span:focus, 
.pagination>.active>span:hover {
  z-index: 2;
  color: #fff;
  cursor: default;
  background-color: #337ab7;
  border-color: #337ab7;
}